.header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.icon-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.header-bascket-btn {
  height: 24px;
  padding: 0;
}

.icon-menu {
  stroke: var(--main-color);
}

.header-logo-main {
  color: var(--main-color);
}

.header-logo-part {
  color: var(--active-color);
}

.bascket-icon {
  fill: rgba(0, 0, 0, 0);
  stroke: var(--main-color);
  transition: stroke var(--transition-duration) var(--transition-time-function);
}

.bascket-icon:hover, .bascket-icon:focus {
  stroke: var(--active-color);
}

.toggle-btn {
  height: 20px;
  width: 46px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
  background-color: var(--additional-color);
  border-radius: 18px;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.toggle-btn:after {
  content: "";
  background-color: var(--secondary-rgb-color);
  width: 18px;
  height: 18px;
  border-radius: 50%;
  position: absolute;
  top: 2px;
  left: 2px;
}

.header-icon-wrapper {
  gap: 14px;
  display: flex;
}

.header-logo {
  letter-spacing: -.64px;
  padding-top: 27, 5px;
  padding-bottom: 27, 5px;
  transition: text-decoration var(--transition-duration) var(--transition-time-function);
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.13;
}

.header-logo:hover, .header-logo:focus {
  text-decoration: underline;
}

.nav-list {
  align-items: center;
  gap: 16px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .header-icon-wrapper {
    align-items: center;
    gap: 14px;
  }

  .nav {
    display: none;
  }

  .burger-menu {
    height: 32px;
    padding-left: 0;
    padding-right: 0;
  }

  .mobile-close-icon {
    stroke: var(--secondary-rgb-color);
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .header-toggle-list {
    display: none;
  }

  .mobile-menu {
    width: 50%;
    height: 100vh;
    z-index: 100;
    background-color: var(--active-color);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity var(--transition-duration) var(--transition-time-function) visibility var(--transition-duration) var(--transition-time-function);
    padding-top: 200px;
    position: fixed;
    top: 0;
    left: 0;
  }

  .header-toggle {
    display: none;
  }

  .mobile-menu-close-btn {
    height: 32px;
    display: block;
  }

  .mobile-nav-list {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .mobile-nav-link {
    color: var(--secondary-color);
    transition: color var(--transition-duration) var(--transition-time-function), border-bottom var(--transition-duration) var(--transition-time-function);
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 1.2;
  }

  .mobile-nav-link:hover, .mobile-nav-link:focus {
    color: #fff;
    border-bottom: 1px solid #fff;
  }

  .mobile-nav-link.current {
    color: #fff;
    font-weight: 500;
  }

  .mobile-menu {
    transition: transform var(--transition-duration) var(--transition-time-function);
    transform: translateX(-100%);
  }

  .mobile-menu.is-open {
    transform: translateX(0);
  }

  .mobile-toggle-icon {
    position: absolute;
    bottom: 205px;
    left: 20px;
  }
}

@media screen and (min-width: 768px) {
  .nav {
    display: flex;
  }

  .header-toggle {
    display: block;
  }

  .nav-list {
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .header-toggle-list {
    display: block;
  }

  .mobile-menu {
    display: none;
  }

  .nav-list {
    align-items: center;
    gap: 16px;
    display: flex;
  }

  .nav-link {
    color: var(--main-color);
    transition: color var(--transition-duration) var(--transition-time-function), text-decoration var(--transition-duration) var(--transition-time-function);
    justify-content: center;
    padding-top: 28px;
    padding-bottom: 25px;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.2;
  }

  .nav-link:hover, .nav-link:focus {
    color: var(--active-color);
    text-decoration: underline;
  }

  .header-wrapper {
    gap: 18px;
  }

  .burger-menu-wrupper {
    display: none;
  }

  .nav-link.current {
    color: var(--active-color);
  }

  .header-logo {
